
import VHeader from '@/components/v-header/v-header'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import axios from '@/api/axiosApi'
import { localUser } from '@/assets/js/local'

export default {
data () {
return {
content: '',
title: '编辑评论'
}
},
mounted () {
this.$refs.commtent.$el.children[1].children[0].focus()
},
components: {
VHeader, XTextarea, Group
},
methods: {
sendComment () {
const _self = this
axios.post(this, '/v1/DynamicRecord', {
dynamicId: _self.$route.params.dynamicId,
userId: localUser.get('id'),
content: _self.content
}, (data) => {
_self.$router.go(-1)
})
}
}
}



